a {
  text-decoration: none;
  color: @special_grey_light;
}

body {
  font-family: "Space Grotesk", "Arial", sans-serif;
  max-width: 1280px;
  margin: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding:0;
  border:0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
